*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.gallery-item {
  height: 450px;
  overflow: hidden;
}

.gallery-image-preview {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.photo-card {
  text-decoration: none;
  color: #2a2a2a;
}
.gallery-image-preview:hover {
  border-radius: 4px;
}

.stats {
  display: flex;
  justify-content: center;
  align-items: center;
}

.stats-item {
  padding: 10px 5px 10px 5px;
  font-size: 15px;
}

.material-icons {
  display: flex;
  justify-content: center;
  align-items: center;

  color: black;
}
